<template>
    <div id="pay">
        <p></p>
        <p>{{this.$store.state.data[this.$store.state.logined].login}}</p>
        <p>{{this.$store.state.logined}}</p>
        <p>{{this.$store.state.data[this.$store.state.logined].name}}</p>
        <p>{{this.$store.state.data[this.$store.state.logined].adr}}</p>
        <p>{{this.$store.state.data[this.$store.state.logined].phone}}</p>
        <el-button type="success" @click="send" round></el-button>
        <el-button type='danger' round @click="logout"></el-button>
    </div>
</template>
<script>
    export default{
        data(){
            return{

            }
        },
        methods:{
                logout(){
                    this.$store.state.logined= -1;
                    this.$router.push('/hi');
                },
                send(){
                    this.$alert(''+this.$store.state.data[this.$store.state.logined].login+'', '', {
                        confirmButtonText: '',
                        callback: action => {
                            this.$message({
                            type: 'success',
                            message: ''
                            });
                        }
                        });
                }
            }
        }
    

</script>
<style></style>


